import React from 'react';
import { connect } from 'react-redux';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Redirect,
} from 'react-router-dom';
import './App.less';
import { routes } from './utils/routes';
import { Spin } from "antd";

const mapState = state => ({
  loading: state.global.isLoading,
})
function App(props) {
  return (
      <Router>
          <Spin
              tip={'Loading...'}
              spinning={props.loading}
              size='large'
              wrapperClassName={'global-spin'}
          >
              <Switch>
                  {
                      routes.map((item) => {
                          const {path, component, exact} = item;
                          return (
                              <Route
                                  path={path}
                                  component={component}
                                  exact={exact}
                                  key={path}
                              />
                          )
                      })
                  }
                  <Redirect to='/404' />
              </Switch>
          </Spin>
      </Router>
  );
}

export default connect(mapState)(App);



