import {
    notFound,
    home,
    test,
} from '../pages/index';

export const routes = [
    {
        path: '/',
        component: home,
        exact: true,
        children: null,
    }, {
        path: '/404',
        component: notFound,
        exact: false,
        children: null,
    }, {
        path: '/test',
        component: test,
        exact: true,
        children: null,
    }
];



