import Loadable from 'react-loadable';
import {LoadingComponent} from '../highOrderComponents/Loading/index';

const notFound = Loadable({
    loader: () => import('./notFound/index'),
    loading: () => null,
});
const home = Loadable({
    loader: () => import ('./home/index'),
    loading: () => LoadingComponent,
})
const test = Loadable({
    loader: () => import('./test/index'),
    loading: () => LoadingComponent,
})

export {
    notFound,
    home,
    test,
};



