import {
    CHANGE_LOADING,
} from '../actions/global';

const initState = Object.assign(
    {},
    {
        isLoading: true,
    },
);

export default (state = initState, action) => {
    switch (action.type) {
        case CHANGE_LOADING:
            console.log(state)
            return {
                ...state,
                isLoading: !state.isLoading,
            }
        default:
            return state;
    }
}



